body {
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto","Ubuntu", "Segoe UI", "Oxygen",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}







.NavigationDiv
{
  background-color: black;
  width: 100%;
  text-align: center;
}

.NavigationDiv ul
{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}


.NavigationDiv li a
{
  display: block;
  color: white;
  float: left;
  padding: 14px 16px;
  text-decoration: none;
}


.TitleStyle
{
  color: white;
  text-align: center;
  margin-top: 300px;
}

.TitleText
{
  font-size: 3rem;
}

.CaptionText
{
  color: white;
  background-color: rgba(0, 0, 0,0.5);
  display: inline-block;
}

.HeadStyle
{
  background-color: white;
  display: inline-block;
  transform: skewX(150deg);
  width: 200px;
}

.HeadStyle h4
{
  margin-left: 10px;
  transform: skewX(-150deg);
  display: inline-block;
  color: black;
}

.HeadStyle .strip
{
  background-color: blue;
  transform: scaleX(150deg);
  
  width: 10px;
  display: inline-block;
}

.ListTypeNone
{
  list-style-type: none;
}


.HomeContent
{
  margin-left: 10px;
  height: 100%;
}

.HomeText
{
  margin: 30px;
  background-color: rgba(0, 0, 0,0.5);
  color: white;
  border-radius: 20px;
}

.HomeTextInner
{
  margin: 8px;
}




